* {}

body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow-y: hidden;
  background-color: #B46060;
}


.chatsList {
  display: inline-block;
  width: 25%;
  padding: 10px;
  height: 90vh;
  background-color: #FFBA86;
  border-radius: 20px;
  margin: 10px;
  vertical-align: text-top;

}

.chat {
  display: inline-block;
  vertical-align: text-top;
  width: 70%;
  padding: 10px;
  height: 90vh;
  overflow: scroll;
  overflow-x: hidden;
  background-color: #FFBA86;
  border-radius: 20px;
  margin-top: 10px;
}

.chat .top {
  position: absolute;
  top: 0;
  background-color: rgb(237, 255, 233);
  width: 70%;
  margin: -10px;
  padding: 10px;
}

.chatButton {
  text-align: left;
  width: 100%;
  padding: 20px;
  background: #79155B;
  border: none;
  border-radius: 20px;
  margin-bottom: 10px;
  font-weight: bold;
  color: white;
}

.chatMessageInput {
  height: 10%;
  position: absolute;
  bottom: 0;
  padding: 10px;
  width: 70%;
  transform: translate(-10px, 0);
}

.chatMessageInput input {
  width: 80%;
  padding: 10px;
  border-radius: 20px;
  background-color: #FFEADD;
  color: #79155B;
  border: none;
}
.chatMessageInput input :focus {
  border: none;
}

.chatMessageInput button {
  width: 10%;
  padding: 10px;
  border-radius: 20px;
  margin-left: 5px;
  background: #79155B;
  color: white;
  font-weight: bold;
  border: none;
}

.popup {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #FFBA86;
  border-radius: 20px;
  color: #79155B;
  padding: 20px;
  text-align: center;
  border: 3px solid #79155B;
}

.popup h3{
  font-size: 30px;
}
.popup input{
  color: #79155B;
  padding: 10px;
  border-radius: 10px;
  border: none;
  font-size: 20px;
  margin: 3px;
}
.popup button{
  background-color: #79155B;
  color: white;
  padding: 10px;
  border-radius: 10px;
  border: none;
  font-size: 20px;
  margin: 3px;
}
.message, .myMessage {
  background-color:#FFEADD;
  margin: 10px;
  padding: 10px;
  border-radius: 10px;
  width: max-content;
}

.myMessage {
  background-color: #A84448;
  color: white;
}

.loadbutton{
  padding: 10px;
  border-radius: 20px;
  margin-left: 5px;
  background: #79155B;
  color: white;
  font-weight: bold;
  border: none;
}

